/* 슬라이드 텍스트 slide_text.css */





.slide__wrap{
  width: 100%;
  overflow: hidden;
  position: relative;
  /* padding: 20vh 0; */
}
/* 오버프로우 히든하면 z인덱스 안먹히는거
어떻게 해결하면 좋을까 */


.slide{
  height: 30px;
  border:  1px solid var(--dark-color);
  background-color: var(--point-color);
  /* transform: scale(1.2) rotate(355deg) translate(2px, -40px); */
}

.slide__box{
  position: relative;
  /* JS에서 스크롤 시 레프트값 조절하여 이동 */
  left: -300px; 

  display: flex;

  height: 100%;
  width: 5500px;
}

.slide__box.box2{
  left: 0; 
  right: 0px;
}

.slide__text{

}

.slide__text span{
  font-size: 2rem;
  font-family: 'LibreBaskerville-Bold';
  line-height: 1.3;
  position: relative;
  top: 7%;
  color: var(--dark-color);
}

.slide__logo{
  height: 100%;
}
.slide__logo img{
  transform: scale(0.3);
  transform-origin: top;


}


.slide_back.slide2{
  background-color: var(--bright-color);
  position: absolute;
  top: 34%;
  /* transform: rotate(8deg); */
 }

.slide.slide2{
  /* transform: scale(1.2) rotate(10deg) translate(2px, -40px); */
}

