/* _____________________  gnb _________________________ */

#header{
  position: relative;
  z-index: 99999;
}


.header{ 
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;

}

.gnb__box{
  height: 100%;
  position: relative;
}

.gnb__menu {
  display: grid;
  height: 100%;
  grid-template-columns: repeat(13, 1fr);
  justify-items: start;

}

.gnb__menu li{
  width: 100%;
  height: 100%;
  display: inline-block;
  background-color: #fff;

  border-left: 1px solid var(--dark-color);
  border-right: 2px solid var(--dark-color);
 }

 .gnb__menu li a{
  position: absolute;
  width: 90px;
  height: 30px;
  text-align: center;
  line-height: 1.9;
  display: inline-block;
  transform: rotate(358deg);
  color: var(--dark-color);

  z-index: 1;
 

  font-family:var(--bold-font);
  font-size: var(--font-size);

}


.gnb__menu li a:hover{
  color: var(--point-color);
  
}

/* 네비게이션 아이템 책갈피 모양 */
.gnb__menu li a::before{
  content: '';

  position: absolute;
  left: 0;
  width: 90px;
  height: 30px;
  background-color: var(--bright-color);
  /* outline: 1px solid var(--dark-color); */
  clip-path: polygon(100% 0%, 90% 45%, 100% 88%, 0 100%, 0 55%, 0 12%);
  z-index: -1;
  transform: rotate(2deg);
}


.gnb__menu li a::after{
  content: '';

  position: absolute;
  left: 0;
  top: -1px;
  width: 91px;
  height: 32px;
  background-color: var(--dark-color);
  /* outline: 1px solid var(--dark-color); */
  clip-path: polygon(100% 0%, 90% 45%, 100% 88%, 0 100%, 0 55%, 0 12%);
  z-index: -2;
  transform: rotate(2deg);
}



/* 네비게시연 아이템 on 되어있는 상태 */
.gnb__item.on.gnb__item::before{
  background-color: var(--dark-color);
}
.gnb__item.on.gnb__item::after{
  background-color: var(--point-color);
}

.gnb__menu li a.gnb__item.on {
  color: var(--dark-color);
}



.gnb__menu li a:hover::before{
  content: '';
  background-color: var(--dark-color);
  
}


.gnb__menu li a::before{
  content: '';

  position: absolute;
  left: 0;
  width: 90px;
  height: 30px;
  background-color: var(--bright-color);
  /* outline: 1px solid var(--dark-color); */
  clip-path: polygon(100% 0%, 90% 45%, 100% 88%, 0 100%, 0 55%, 0 12%);
  z-index: -1;
  transform: rotate(2deg);
}


.gnb__menu li a::after{
  content: '';

  position: absolute;
  left: 0;
  top: -1px;
  width: 91px;
  height: 32px;
  background-color: var(--dark-color);
  /* outline: 1px solid var(--dark-color); */
  clip-path: polygon(100% 0%, 90% 45%, 100% 88%, 0 100%, 0 55%, 0 12%);
  z-index: -2;
  transform: rotate(2deg);
}



/* 네비게시연 아이템 on 되어있는 상태 */
.gnb__item.on.gnb__item::before{
  background-color: var(--point-color);
}
.gnb__item.on.gnb__item::after{
  background-color: var(--bright-color);
}






 /* 네비게이션 세로 줄에 추가 라인 */
 .vertical-lines {
  position: relative;
}

.vertical-lines::before,
.vertical-lines::after {
  content: "";
  position: absolute;
  width: 5%; 
  background-color: var(--dark-color); 
  height: 100%;
}

.vertical-lines::before {
  left: 30%; 

}

.vertical-lines::after {
  left: 60%;

}

