  width: 100%;

  overflow: hidden;
  position: relative;

}

.marquee div {
  display: block;
  width: 200%;
  height: 30px;

  position: absolute;
  overflow: hidden;
  background-color: var(--bright-color);
  animation: marquee-ani 10s linear infinite;
}

.marquee span {
  float: left;
  width: 50%;
  color: var(--point-color);
  font-size: 1.2rem;
  letter-spacing: .3rem;
}




