/* 공지사항 CSS notice.css */


#notice{
  position: relative;
}

.notice{
  background-color: var(--dark-color);
  /* border-radius: 20px; */
}

.notice .tit{
  color: var(--bright-color);
}



.accordion{
  text-align: right;
}
.accordion h3{
  font-size: 1.6rem;
  border-top: 1px solid var(--color1);
  padding: 1% 5%;
  /* cursor: pointer; */
  font-family: 'SUIT Variable-l';
}


.accordion p,ul,ol,li,pre{
  font-family: 'SUIT Variable-l';
  font-size: 1.5rem;
  line-height: 1.3;
  color: var(--bright-color);
}

.accordion p{
 display: inline-block; 
}

.notice__box{
  width: 100%;
  height: 70%;
  /* margin-left: 5%; */
  margin-top: 5%;
}

.notice__title{
  background-color: var(--dark-color);
  border-color: var(--bright-color);
  color: var(--bright-color);
  font-weight: 300;
}

.notice__title:hover{
  background-color: var(--point-color);
  color: var(--dark-color);
  transition: .5s;
  font-weight: bold;
}


.notice_content{
background-color: var( --color3) !important;
height:auto !important;
padding: 3% 5% 3% 30%;
white-space: pre-wrap;
}